.info {
  display: flex;
  justify-content: space-between;

  &__heading {
    max-width: 314px;
    font-weight: 300;
    text-transform: uppercase;
    line-height: 60px;
    font-size: 48px;
    margin: 0;
  }

  &__text {
    max-width: 564px;

    > :first-child {
      margin-top: 0;
    }

    > :last-child {
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width: #{$small-screen-breakpoint}) {
  .info {
    flex-direction: column;

    &__heading {
      margin-bottom: 20px;
      line-height: 40px;
      font-size: 32px;
    }

    &__text {
      font-size: $font-size-main;
    }
  }
}
